export default {
	components: {
	},
	data() {
		return {
			list: [],
			lodatext: "上拉加载更多",
			emit: 10,
			devid: ""
		}
	},
	onLoad() {
		this.__init()
	},
	onReachBottom() {
		this.lodatext = "加载中..."
		this.emit += 10 
		console.log(this.emit);
		this.__init()
	},
	methods: {
		searchbtn() {
			this.__init()
		},
		async __init(callback = false) {
			this.$H.post("/agent/", {
				user_id: "100003",
				token: "dXQyMDIwMDMyMzExMjM0OTMzNzM3ODAz",
				opt: "device_list",
				slimit: "0",
				elimit: `${this.emit}`,
				device_id: this.devid
			}).then((data) => {
				console.log(data);
				this.list = data.arr
				
				// 恢复加载状态
				this.lodatext = this.list.length < this.emit ? "没有更多了" :  "上拉加载更多"
			}).catch(() => {
				console.log("catch error");
			})
		}
	}
	
	
}
